@supports (zoom:2) {
	input[type="radio"],  input[type=checkbox]{
	zoom: 2;
	}
}
@supports not (zoom:2) {
	input[type="radio"],  input[type=checkbox]{
		-webkit-transform: scale(2);
		    -ms-transform: scale(2);
		        transform: scale(2);
		margin: 15px;
	}
}

.controlpanel{
	/*display: flex;*/
	margin-bottom: 1rem;
	border-bottom: .2rem solid red;
}

button.selectionToggle{
	padding: 10px;
	font-size: 23px;
	border: none;
	background: cadetblue;
	width: 100%;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}
.selectionSearch{
	padding: 10px ;
	text-align: center;
	border: none;
	font-size: 23px;
	width: 100%;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.coinlist{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.coinelement{
  display: inline-block;
  width: 50%;
  padding: 7px 0 7px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

@media(min-width: 768px) {
  .coinelement {
    width: 50%;
  }
}
@media(min-width: 968px) {
  .coinelement {
    width: 30%;
  }
}
@media(min-width: 1200px) {
  .coinelement {
    width: 25%;
  }
}

.coinelement input{
	margin-left: 1rem;
}



